.InformationProduct {
  &-wrapper {
    margin-top: 60px;
  }
  &-header {
    width: 300px;
    font-size: 24px;
    font-weight: 500;
    &__wrapper {
      display: flex;
      align-items: center;
    }
    &__price {
      font-size: 40px;
      margin-right: 8px;
    }
    &__recuring {
      display: inline-flex;
      flex-direction: column;
      font-size: 16px;
      height: 40px;
    }
  }

  &-body {
    margin-top: 30px;
  }

  &-prices {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__label {
      color: var(--nightblue-100);
      font-weight: 500;
      p {
        font-weight: 400;
      }
    }
    &__itemPrice {
      font-weight: 500;
      color: var(--nightblue-100);
      font-size: 16px;
    }
  }

  &-coupon {
    margin-top: 30px;
    &__btn {
      height: 45px;
      width: 400px;
    }
    &__input {
      color: var(--nightblue-100);
      width: 400px;
    }
  }

  &-total {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--nightblue-100);
    font-weight: 500;
    font-size: 24px;
  }
}
