.CreditCard {
  &-tabslist {
    text-align: center;
    background: var(--violet-100);
    box-shadow: 0px 5px 20px rgba(0, 95, 113, 0.05);
    border-radius: 30px;
    width: 324px;
    height: 46px;
    display: flex;
    list-style: none;
    align-items: center;
    justify-content: center;
    margin: auto;
    cursor: pointer;
    color: #fff;
    padding-inline: 4px;

    .CreditCard-tabsItem {
      width: 157px;
      height: 36px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .CreditCard-tabstitle {
      text-align: center;
      font-weight: 500;
      font-size: 15px;
    }

    .react-tabs__tab--selected {
      background: var(--violet-10) !important;
      color: var(--violet-120) !important;
      border-radius: 30px !important;
    }
  }
  &-tabs-panel {
    margin-top: 24px;
    .CreditCard-tabsPanelItems__label {
      font-weight: 500;
      font-size: 13px;
      color: var(--nightblue-100);
    }
  }

  &-totalPay {
    margin-top: 24px;
    width: 100%;
    background: var(--violet-10);
    border-radius: 16px;
    padding: 20px;
    color: var(--nightblue-100);
    font-size: 27px;
    font-weight: 500;
    .CreditCard-price {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .CreditCard-content {
      font-size: 13px;
    }
  }
  &-btnSubmit {
    margin: 24px auto 0px;
    width: 343px;
    height: 45px;
  }
}
